import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';
import {Router} from '@angular/router';
import {ApiService} from '../../api.service';
import {SharedServiceService} from '../../shared-service.service';
import {StorageService} from '../storage.service';
import {appConstants} from '../../../assets/constants/app-constants';

@Component({
  selector: 'service-list',
  templateUrl: './services-list.page.html',
  styleUrls: ['./services-list.page.scss'],
})

export class ServicesListPage implements OnInit{
  @Output() updateAmount = new EventEmitter<object>();
  @Input() isRefreshed;
  constructor(private storageService: StorageService, private sharedService: SharedServiceService, private router: Router, private adminService: ApiService) { }
  show = '';
  show1 = false;
  mainServices = [];
  services = [];
  amountPurchased = 0;
  selectedServices = [];
  ngOnInit() {
    this.getMainServices();
  }
  //
  // ngOnChanges(changes: SimpleChanges) {
  //   this.getMainServices();
  // }

  ionViewWillLeave() {
      this.getMainServices();
  }

  ionViewWillEnter() {
      this.getMainServices();
  }
  onBookAppointment() {
    this.router.navigate(['/schedule-appointment']);
  }

  getServices() {
    this.adminService.getAllServices().subscribe(
        res => this.getAllServicesSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getMainServices() {
    this.sharedService.showSpinner.next(true);
    this.adminService.getAllServiceTypes().subscribe(
        res => this.getMainServicesSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getAllServicesSuccess(res) {
    this.services = res;
    this.sharedService.showSpinner.next(false);
    this.storageService.getStoredValue(appConstants.SELECTED_SERVICES).then((val: any) => {
      if(val && val.length > 0) {
        this.mainServices = this.mainServices.map((serv) => {
          const storedServiceIds: any = val.map(v => v.id);
          serv.services = this.services.filter(v => v.serviceTypeId === serv.id)
              .map((service) => {
                service.isChecked = !!(storedServiceIds.includes(service.id));
                return service;
              });
          return serv;
        });
        this.amountPurchased = val.reduce((a, b) => {
          return a + b.price;
        }, 0);
        const data = {amountPurchased: this.amountPurchased, selectedServices: val};
        this.updateAmount.emit(data);
      } else {
        this.mainServices = this.mainServices.map((serv) => {
          serv.services = this.services.filter(v => v.serviceTypeId === serv.id);
          serv.show = false;
          return serv;
        });
      }
    });
  }

  getMainServicesSuccess(res) {
    this.mainServices = res;
    this.sharedService.showSpinner.next(false);
    this.getServices();
  }

  onSelectServices(service) {
    service.isChecked = !service.isChecked;
    this.amountPurchased = this.mainServices.reduce((acc, first) => {
      const servicePrice = first.services.reduce((acc2, second) => {
        if (second.isChecked) {
          acc2 = acc2 + parseInt(second.price, 10);
        }
        return acc2;
      }, 0);
      acc = parseInt(acc, 10) + parseInt(servicePrice, 10);
      return acc;
    }, 0);
    this.selectedServices = this.mainServices.reduce((acc, first) => {
      let selectedServices = [];
      selectedServices = first.services.filter(v => v.isChecked);
      if (selectedServices.length > 0) { acc.push(selectedServices); }
      return acc;
    }, []);

    const data = {amountPurchased: this.amountPurchased, selectedServices: [].concat.apply([], this.selectedServices)};
    this.updateAmount.emit(data);
  }

}
